.header {
  position: relative;
  background: linear-gradient(rgba(255,255,255,.3), rgba(255,255,255,.3)), url(/Templewebsite/static/media/湖西天后.36a5696b.jpg);
  background-size: cover;
  background-position: top center;
  height: 90vh;
  align-items: center;
  text-align: center;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.main-info h1 {
  font-size: 60px;
	color: #FDFEFF;
  letter-spacing: 10px;
  text-shadow: 1px 1px 1px #000, 
               3px 3px 5px grey; 
}

.main-info p {
  font-size: 20px;
  color: #FDFEFF;
  letter-spacing: 10px;
  text-shadow: 1px 1px 1px #000, 
               3px 3px 5px grey; 
}

.main-info button {
  background-color: #C89832 !important;
  position: absolute;
  top: 400px;
}

@media only screen and (max-width: 600px) {
  .main-info h1 {
    font-size: 43px;
  }
}
