.templecards {
    margin-top: 50px;
    margin-bottom: 30px;
}

.templecard {
    text-align: center;
    position: relative;
}

.templecard img{
    width: 100%;
    max-height: 255px;
    border-radius: 15px;
    display: block;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    opacity: 1;
}

.templecard p {
    color: #FDFEFF;
    position: absolute;
    bottom: 10px;
    left: 25px;
    font-size: 30px;
}

.templecard-btn {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    color: #FDFEFF;
}

.templecard-btn button {
    width: 150px;
    height: 50px;
    background: transparent;
    border: solid 3px #FDFEFF;
    color: #FDFEFF !important;
    text-shadow: 1px 1px 1px #000, 
               3px 3px 5px grey !important; 
}

.templecard:hover img{
    opacity: 0.3;
}

.templecard:hover .templecard-btn {
    opacity: 1;
}

.templecard {
    padding: 10px;
}
